<template>
    <!-- 切换按钮 -->
    <div :class="currentClass" @click="toggleSidebar">
      <span></span>
      <span></span>
      <span></span>
    </div>

    <!-- 侧边栏 -->
    <div class="sidebar color-change" :class="{ 'active': isSidebarOpen }">
      <ul>
        <li><router-link to="/" @click="closeSidebar">收藏</router-link></li>
        <li><router-link to="/blog" @click="closeSidebar">博客</router-link></li>
        <li><router-link to="/game" @click="closeSidebar">游戏</router-link></li>
        <li><router-link to="/contact" @click="closeSidebar">留言</router-link></li>
        <li><router-link to="/login" @click="closeSidebar">登录</router-link></li>
      </ul>
    </div>

    <!-- 遮罩层 -->
    <div v-if="isSidebarOpen" class="overlay" @click="toggleSidebar"></div>
</template>

<script>
export default {
  data() {
    return {
      isSidebarOpen: false,
      currentClass: 'open',
    };
  },
  methods: {
    toggleSidebar() {
      this.isSidebarOpen = !this.isSidebarOpen;
      this.currentClass = this.currentClass === 'open' ? 'close' : 'open';
    },
    closeSidebar() {
      this.isSidebarOpen = false;
      this.currentClass = this.currentClass === 'open' ? 'close' : 'open';
    }
  },
};
</script>

<style scoped>
.open {
  position: relative;
  top: 7px;
  left: 5px;
  cursor: pointer;
  z-index: 9999;
  overflow: hidden;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  backdrop-filter: blur(10px);
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  transition: all 0.5s ease;
}

.close{
  position: relative;
  top: 7px;
  left: 5px;
  cursor: pointer;
  z-index: 9999;
  overflow: hidden;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  backdrop-filter: blur(10px);
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  transition: all 0.5s ease;
}

.open:hover,.close:hover{
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.555),3px 3px 15px rgba(255, 255, 255, 0.352);
  transition: all 0.5s ease;
}

.open span {
  display: block;
  width: 30px;
  height: 3px;
  margin: 4px 0;
  background-color: #ffffff;
  border-radius: 20px;
  transition: all 0.5s ease;
}

.close span:nth-child(1) {
  display: block;
  width: 30px;
  height: 3px;
  margin: -1.25px 0;
  background-color: #000000a3;
  transform: rotate(135deg);
  border-radius: 20px;
  transition: all 0.5s ease;
}

.close span:nth-child(2) {
  display: block;
  width: 30px;
  height: 3px;
  margin: -1.25px 0;
  background-color: #000000a3;
  transform: rotate(-135deg);
  border-radius: 20px;
  transition: all 0.5s ease;
}

/* 侧边栏样式 */
.sidebar {
  position: fixed;
  top: 0;
  left: -300px;
  width: 300px;
  height: 100%;
  backdrop-filter: blur(10px);
  background-color: #2fdfbc74;
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.884),2px 2px 15px rgba(102, 172, 158, 0.544);
  color: white;
  transition: left 0.5s ease;
  z-index: 9998;
  padding: 100px;
}

.sidebar.active {
  left: 0;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar ul li {
  padding: 15px 0;
}

.sidebar ul li a {
  --glow-color: rgba(231, 237, 108, 0.767);
  --glow-spread-color: rgba(232, 123, 60, 0.781);
  --enhanced-glow-color: rgba(229, 192, 159, 0.77);
  --btn-color: rgb(0, 0, 0);
  margin: 0 auto;
  display: block;
  width: 50px;
  height: 50px;
  line-height:50px;
  border-radius: 20%;
  font-weight: bold;
  font-size: 20px;
  color: rgba(221, 221, 221, 0.945);
  cursor: pointer;
  text-decoration: none;
  backdrop-filter: blur(10px);
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.555),3px 3px 15px rgba(255, 255, 255, 0.352);
  text-shadow: 0 0 .5em var(--glow-color);
  position: relative;
  transition: all 0.3s ease;
}

.sidebar ul li a:active {
  box-shadow: 0 0 0.6em .25em var(--glow-color),
        0 0 2.5em 2em var(--glow-spread-color),
        inset 0 0 .5em .25em var(--glow-color);
}

.sidebar ul li a:hover {
  width: 100px;
  border-radius: 50px;
  transition: all 0.3s ease;
  color: var(--btn-color);
  background-color: var(--glow-color);
  box-shadow: 0 0 1em .25em var(--glow-color),
          0 0 4em 2em var(--glow-spread-color),
          inset 0 0 .75em .25em var(--glow-color);
}

/* 遮罩层样式 */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9997;
}
@import '@/assets/css/dark.css';
</style>