<template>
<div class="background color-change">
  <span class="abc"></span><span class="abc"></span><span class="abc"></span>
  <div class="bubbles">
    <span style="--i:11"></span>
    <span style="--i:12"></span>
    <span style="--i:21"></span>
    <span style="--i:26"></span>
    <span style="--i:15"></span>
    <span style="--i:22"></span>
    <span style="--i:10"></span>
    <span style="--i:19"></span>
    <span style="--i:23"></span>
    <span style="--i:13"></span>
    <span style="--i:17"></span>
    <span style="--i:25"></span>
    <span style="--i:14"></span>
    <span style="--i:24"></span>
    <span style="--i:16"></span>
    <span style="--i:18"></span>
    <span style="--i:35"></span>
    <span style="--i:29"></span>
    <span style="--i:27"></span>
    <span style="--i:28"></span>
    <span style="--i:11"></span>
    <span style="--i:12"></span>
    <span style="--i:21"></span>
    <span style="--i:26"></span>
    <span style="--i:8"></span>
    <span style="--i:22"></span>
    <span style="--i:32"></span>
    <span style="--i:19"></span>
    <span style="--i:23"></span>
    <span style="--i:13"></span>
    <span style="--i:17"></span>
    <span style="--i:25"></span>
    <span style="--i:14"></span>
    <span style="--i:24"></span>
    <span style="--i:16"></span>
    <span style="--i:31"></span>
    <span style="--i:20"></span>
    <span style="--i:29"></span>
    <span style="--i:27"></span>
    <span style="--i:28"></span>
    <span style="--i:11"></span>
    <span style="--i:12"></span>
    <span style="--i:21"></span>
    <span style="--i:26"></span>
    <span style="--i:15"></span>
    <span style="--i:22"></span>
    <span style="--i:10"></span>
    <span style="--i:19"></span>
    <span style="--i:23"></span>
    <span style="--i:13"></span>
    <span style="--i:17"></span>
    <span style="--i:25"></span>
    <span style="--i:3"></span>
  </div>
  <div class="comment-container">
    <h2>留言板</h2>
    <form @submit.prevent="submitComment">
      <div class="input-group">
          <textarea v-model="message" autocomplete="off" class="input" required></textarea>
          <label class="user-label">请输入留言内容</label>
      </div>  
      <div class="input-group">
          <input type="email" v-model="email" autocomplete="off" class="input" required>
          <label class="user-label">邮箱</label>
      </div>
      <div class="input-group">
          <input type="text" v-model="wechat" autocomplete="off" class="input" required>
          <label class="user-label">微信号</label>
      </div>
      <button type="submit">留言</button>
    </form>
    <div v-if="showSuccess" class="success-message">
      留言提交成功！
    </div>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      message: '',
      email: '',
      wechat: '',
      showSuccess: false,
      captchaPassed: false,
    };
  },
  methods: {
    async submitComment() {
      // 检查是否填写了邮箱或微信号
      if (!this.email && !this.wechat) {
        alert("邮箱和微信号至少填写一项。");
        return;
      }
      // 触发腾讯验证码
      if (window.TencentCaptcha) {
        const captcha = new window.TencentCaptcha('199472992', async (res) => {
          if (res.ret === 0) {
            this.captchaPassed = true;
            await this.sendEmail(); // 验证通过后发送邮件
          } else {
            alert("验证失败，请重试。");
          }
        });
        captcha.show();
      } else {
        console.error('TencentCaptcha is not available');
      }
    },
    async sendEmail() {
      const emailContent = `
        <p><strong>留言内容:</strong> ${this.message}</p>
        <p><strong>邮箱:</strong> ${this.email || '未填写'}</p>
        <p><strong>微信号:</strong> ${this.wechat || '未填写'}</p>
      `;
      try {
        await fetch("https://api.emailjs.com/api/v1.0/email/send", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            service_id: "service_6udxhdj",  // 替换为你的服务 ID
            template_id: "template_k2cmi0e",  // 替换为你的模板 ID
            user_id: "yUorgPxvESOYjLGnC",  // 替换为你的 Public Key
            template_params: {
              to_email: "1824893195@qq.com",  // 指定接收邮件的目标邮箱
              from_name: "留言板",
              message_html: emailContent,  // 模板中 {{message_html}} 占位符的值
              email: this.email || '未填写',  // 如果你的模板中需要 email 参数
              wechat: this.wechat || '未填写',  // 如果你的模板中需要 wechat 参数
            },
          }),
        });
        console.log("邮件发送成功");
        setTimeout(() => {
          this.showSuccess = true; // 延迟 1 秒后显示成功提示
          setTimeout(() => {
            this.showSuccess = false;
          }, 3000); // 3 秒后隐藏成功提示
        }, 500); // 延迟 0.5 秒后显示提示信息
        // 重置表单和验证状态
        this.message = '';
        this.email = '';
        this.wechat = '';
        this.captchaPassed = false;
      } catch (error) {
        console.error("发送邮件失败：", error);
      }
    },
  },
  mounted() {
  const inputs = document.querySelectorAll('.input, textarea');

    inputs.forEach(input => {
      // 监听输入框的输入事件
      input.addEventListener('input', () => {
        const label = input.nextElementSibling;
        if (input.value.trim() !== '') {
          label.classList.add('label-up');
        } else {
          label.classList.remove('label-up');
        }
      });
      // 监听输入框的聚焦事件
      input.addEventListener('focus', () => {
        const label = input.nextElementSibling;
        label.classList.add('label-up');
      });
      // 监听输入框的失焦事件
      input.addEventListener('blur', () => {
        const label = input.nextElementSibling;
        if (input.value.trim() === '') {
          label.classList.remove('label-up');
        }
      });
    });
  },
};
</script>

<style scoped>
.background{
    width: 100%;
    height: 70%;
    margin: 0;
    padding: 50px;
    background-color: #0a6e62;
    position: relative;
}
.abc{
  background: #fff;
  position: absolute;
  top: 117px;
  left: 144px;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  box-shadow: 1px 1px 50px #fff;
}
.abc:nth-child(1){
  top: 90px;
  left: 1000px;
  width: 50px;
  height: 50px;
  z-index: 30;
  background: rgb(51, 213, 103);
  animation: abc ease-in-out 2s infinite alternate;
}
.abc:nth-child(2){
  top: 360px;
  left: 960px;
  width: 200px;
  height: 200px;
  z-index: 40;
  background: orange;
  animation: abc ease-in-out 2.5s infinite alternate;
}
.abc:nth-child(3){
  top: 40px;
  left: 370px;
  width: 120px;
  height: 120px;
  z-index: 51;
  background: purple;
  animation: abc ease-in-out 3s infinite alternate;
}
@keyframes abc {
  0% {transform: translateY(0);}
  100% {transform: translateY(-30px);}
}
.comment-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  backdrop-filter: blur(10px);
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  font-family: 'Arial', sans-serif;
  position: relative;
  z-index: 50;
  animation: am ease-out 1s 0.2s backwards;
}
@keyframes am{
  0%  {transform: translateY(8px);opacity: 0;}
  100%{transform: translateY(0);opacity: 1;}
}
h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #fff;
}
form {
  display: flex;
  flex-direction: column;
}
/* From Uiverse.io by alexruix */ 
.input-group {
  display: flex;
  flex-direction: column; /* 保证垂直排列 */
  width: 100%; /* 让input-group的宽度为100% */
  position: relative; /* 使label可以绝对定位 */
}
.input {
  border: solid 1.5px #9e9e9e;
  border-radius: 1rem;
  font-size: 1rem;
  transition: border 150ms cubic-bezier(0.4,0,0.2,1);
}
.user-label {
  position: absolute;
  left: 25px;
  color: #e8e8e8;
  pointer-events: none;
  transform: translateY(1rem);
  transition: 150ms cubic-bezier(0.4,0,0.2,1);
  background-color: #21212100; /* 背景透明 */
  padding: 0 .2em; /* 适应 label 大小 */
  font-weight: normal; /* 普通字体 */
}
/* 当 input 有内容或被聚焦时 label 上移 */
.label-up {
  transform: translateY(-50%) scale(0.8);
  color: #ffffff;
}
/* 当 input 聚焦时，边框变色 */
.input:focus, input:valid {
  outline: none;
  border: 1.5px solid #1ae8a3;
}
/* 当 input 被聚焦或内容有效时，label 上移 */
.input:focus ~ .user-label, input:valid ~ .user-label {
  transform: translateY(-50%) scale(0.8);
  background-color: #21212100;
  padding: 0 .2em;
  font-weight: bold;
  color: #ffffff;
}
/* ^^^ */
textarea {
  resize: none;
  height: 100px;
  margin-bottom: 15px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  font-size: 16px;
  background: transparent;
  outline: none;
  color: #fff;
  transition: all 0.3s ease;
  
}
input[type="email"],
input[type="text"] {
  margin-bottom: 15px;
  padding: 10px;
  border: none;
  border-radius: 15px;
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  font-size: 16px;
  background: transparent;
  outline: none;
  color: #fff;
  transition: all 0.3s ease;
}
::placeholder{
	color:#fff;
	font-size:16px;
}
button {
  width: 70px;
  height: 70px;
  margin: 0 auto;
  border: none;
  border-radius: 50%;
  font-weight: bold;
  color: #eee;
  font-size: 20px;
  cursor: pointer;
  background:rgba(255,255,255,0.1) ;
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  transition: background-color 0.3s ease;
  transition: all 0.3s ease;
}
input[type="email"]:hover,input[type="text"]:hover,textarea:hover {
  background-color: #5c627b;
  transition: all 0.3s ease;
}
button:hover{
  width: 150px;
  color: wheat;
  border-radius: 50px;
  background-color: #5c627b;
  transition: all 0.4s ease;
}
.success-message {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(76, 175, 80, 0.9);
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  animation: floatUp 3s ease-in-out;
  z-index: 1999;
}
@keyframes floatUp {
  0% {
    opacity: 0;
    transform: translate(-50%, 20px);
  }
  50% {
    opacity: 1;
    transform: translate(-50%, -10px);
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -30px);
  }
}
.bubbles{
  position: relative;
  display: flex;
}
.bubbles span{
  position: relative;
  width: 20px;
  height: 20px;
  background-color: #4fdcc9;
  margin: 0 4px;
  border-radius: 50%;
  box-shadow: 0 0 0 10px #4fa1dc44,0 0 50px #4fdcc9,0 0 100px #4fdcc9;
  animation: animate 15s linear infinite;
  animation-duration: calc(100s / var(--i));
}
.bubbles span:nth-child(even){
  background: #f1844d;
  box-shadow: 0 0 0 10px #ff5a2d44,0 0 50px #f1844d,0 0 100px #f1844d;
}
@keyframes animate{
  0%{
    transform: translateY(70vh) scale(0);
  }
  100%{
    transform: translateY(-15vh) scale(1);
  }
}
@import '@/assets/css/dark.css';
</style>