        { image: require('@/assets/img/album/47.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/48.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/49.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/50.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/51.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/52.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/53.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/54.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/55.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/56.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/57.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/58.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/59.png'), title: '《》', description: '作者：' },
        { image: require('@/assets/img/album/60.jpg'), title: '《》', description: '作者：' },
      ],
      itemsPerPage: 20, // 每页显示的项目数
      currentMoviePage: 1,
      currentPhotoPage: 1,
      currentAlbumPage: 1
    };
  },
  computed: {
    currentMovies() {
      // 先过滤出符合条件的电影列表
        const filteredMovies = !this.selectedCountry || this.selectedCountry === '全部'
        ? this.movies
        : this.movies.filter(movie => movie.country === this.selectedCountry);
      
      // 然后根据 searchId 过滤电影
        const searchFilteredMovies = this.searchId
        ? filteredMovies.filter(movie => movie.id === this.searchId)
        : filteredMovies;

      // 然后进行分页处理
        const start = (this.currentMoviePage - 1) * this.itemsPerPage;
        const end = start + this.itemsPerPage;

      // 返回当前页的电影
        return searchFilteredMovies.slice(start, end);
    },
    currentPhotos() {
      const start = (this.currentPhotoPage - 1) * this.itemsPerPage;
      const end = this.currentPhotoPage * this.itemsPerPage;
      return this.photos.slice(start, end);
    },
    currentAlbums() {
      const start = (this.currentAlbumPage - 1) * this.itemsPerPage;
      const end = this.currentAlbumPage * this.itemsPerPage;
      return this.albums.slice(start, end);
    },
    maxMoviePages() {
      return Math.ceil(this.movies.length / this.itemsPerPage);
    },
    maxPhotoPages() {
      return Math.ceil(this.photos.length / this.itemsPerPage);
    },
    maxAlbumPages() {
      return Math.ceil(this.albums.length / this.itemsPerPage);
    }
  },
  methods: {
    toggleDropdown() {
      this.isOpen = !this.isOpen;
    },
    selectCountry(country) {
      this.selectedCountry = country;
      this.isOpen = false;
    },
    introduction(index, clickedButton) {
      const displayBars = document.querySelectorAll('.display_bar');
      displayBars.forEach(displayBar => displayBar.classList.remove('active'));

      const buttons = document.querySelectorAll('.button');
      buttons.forEach(btn => btn.classList.remove('active'));

      document.getElementById(`display_bar${index + 1}`).classList.add('active');
      clickedButton.classList.add('active');
      this.selectedIndex = index;

      // 重置页码
      if (index === 0) this.currentMoviePage = 1;
      if (index === 1) this.currentPhotoPage = 1;
      if (index === 2) this.currentAlbumPage = 1;
    },
    prevPage(category) {
      if (category === 'movies' && this.currentMoviePage > 1) {
        this.currentMoviePage--;
        window.scrollTo(0, 0);
      }
      if (category === 'photos' && this.currentPhotoPage > 1) {
        this.currentPhotoPage--;
        window.scrollTo(0, 0);
      }
      if (category === 'albums' && this.currentAlbumPage > 1) {
        this.currentAlbumPage--;
        window.scrollTo(0, 0);
      }
    },
    nextPage(category) {
      if (category === 'movies' && this.currentMoviePage < this.maxMoviePages) {
        this.currentMoviePage++;
        window.scrollTo(0, 0);
      }
      if (category === 'photos' && this.currentPhotoPage < this.maxPhotoPages) {
        this.currentPhotoPage++;
        window.scrollTo(0, 0);
      }
      if (category === 'albums' && this.currentAlbumPage < this.maxAlbumPages) {
        this.currentAlbumPage++;
        window.scrollTo(0, 0);
      }
    }
  },
  mounted() {
    const firstButton = this.$refs.firstButton;
    if (firstButton) {
      this.introduction(0, firstButton);
    }
  }
};
</script>
  
