<template>
    <header class="color-change">
        <SidebarComponent />
        <div class="toggle-container">
            <input type="checkbox" id="dark-mode-toggle" class="toggle-checkbox" ref="darkModeToggle">
            <label for="dark-mode-toggle" class="toggle-label"></label>
            <span>夜晚模式</span>
        </div>
        <div>
            <img src="@/assets/img/kong.png" @click="goToHomePage" alt="空">
            <img src="@/assets/img/kongxing555.png" class="kongxing" @click="goToHomePage" alt="空行555">
            <SearchBar @search-id="handleSearchId" />
            <MusicPlayer />
        </div>
    </header>
    <router-view :searchId="searchId" />
    <footer id="contact">
        <div>
            <p>&copy; 2024 王梓瑞 空行555|1824893195@qq.com</p>
        </div>
    </footer>
</template>

<script>
    import MusicPlayer from '@/components/MusicPlayer.vue';
    import SearchBar from '@/components/SearchBar.vue';
    import SidebarComponent from '@/components/SidebarComponent.vue'
    import initDarkMode from '@/scripts/dark.js';
    export default {
      components: {
        SidebarComponent,
        SearchBar,
        MusicPlayer
      },
      data() {
        return {
          searchId: null,
        };
      },
      methods: {
        handleSearchId(id) {
          this.searchId = id;
        },
        goToHomePage() {
        this.$router.push('/').then(() => {
        // 确保页面跳转完成后刷新页面
        window.location.reload();
          });
        }
      },
      mounted() {
        // 获取 darkModeToggle 元素
        const toggle = this.$refs.darkModeToggle;
        if (toggle) {
            initDarkMode(toggle);
        }
      },
    }
</script>

