        { id: 40, title: '王家卫：《花样年华》', path: '/film/id' },
        { id: 41, title: '王家卫：《重庆森林》', path: '/film/id' },
        { id: 42, title: '胡金铨：《侠女》', path: '/film/id' },
        { id: 43, title: '胡金铨：《龙门客栈》', path: '/film/id' },
        { id: 44, title: '胡金铨：《忠烈图》', path: '/film/id' },
        { id: 45, title: '科恩兄弟：《冰血暴》', path: '/film/id' },
        { id: 46, title: '科恩兄弟：《逃狱三王》', path: '/film/id' },
        { id: 47, title: '科恩兄弟：《老无所依》', path: '/film/id' },
        { id: 48, title: '科恩兄弟：《醉乡民谣》', path: '/film/id' },
        { id: 49, title: '科恩兄弟：《巴斯特·斯克鲁格斯的歌谣》', path: '/film/id' },
        { id: 50, title: '韦斯·安德森：《布达佩斯大饭店》', path: '/film/id' },
        { id: 51, title: '希尔万·肖默：《疯狂约会美丽都》', path: '/film/id' },
        { id: 52, title: '李安：《卧虎藏龙》', path: '/film/id' },
        { id: 53, title: '是枝裕和：《小偷家族》', path: '/film/id' },
        { id: 54, title: '安德烈·塔可夫斯基：《飞向太空》', path: '/film/id' },
        { id: 55, title: '安德烈·塔可夫斯基：《乡愁》', path: '/film/id' },
        { id: 56, title: '安德烈·塔可夫斯基：《安德烈·卢布缪夫》', path: '/film/id' },
        { id: 57, title: '姜文：《阳光灿烂的日子》', path: '/film/id' },
        { id: 58, title: '雷德利·斯科特：《异形》', path: '/film/id' },
        { id: 59, title: '马克·奥斯本：《小王子》', path: '/film/id' },
        { id: 60, title: '卡尔·齐曼：《吹牛伯爵历险记》', path: '/film/id' },
        { id: 61, title: '阿巴斯：《樱桃的滋味》', path: '/film/id' },
        { id: 62, title: '布列松：《钱》', path: '/film/id' },
        { id: 63, title: '西奥·安哲罗普洛斯：《雾中风景》', path: '/film/id' },
        { id: 64, title: '西奥·安哲罗普洛斯：《永恒和一日》', path: '/film/id' },
        { id: 65, title: '西奥·安哲罗普洛斯：《哭泣的草原》', path: '/film/id' },
        { id: 66, title: '阿巴斯：《随风而逝》', path: '/film/id' },
        //博客
        { id: 67, title: '我的博客标志', path: '/blog/:id' }
        ],
        showDropdownList: false, // 控制下拉列表的显示
        hideDropdownTimeout: null, // 控制隐藏下拉列表的延迟
      };
    },
    computed: {
      filteredPostsQuery() {
        if (this.query === '') {
          return [];
        }
          const lowerCaseQuery = this.query.toLowerCase();
          return this.filteredPosts.filter(post => 
          post.title.toLowerCase().includes(lowerCaseQuery)
        );
      }
    },
    methods: {
      onSearch() {
        this.$emit('search-query', this.query);
        if (this.query) {
          this.showDropdown();
        } else {
          this.hideDropdown();
        }
      },
      onEnter() {
        if (this.query) {
          // 如果输入框有内容，则选择第一个搜索结果
          if (this.filteredPosts.length > 0) {
            this.selectPost(this.filteredPosts[0].id);
          }
        } else {
          // 如果输入框为空，刷新页面
          window.location.reload();
        }
      },
      selectPost(postId) {
        // 根据 postId 查找对应的标题
        const selectedPost = this.filteredPosts.find(post => post.id === postId);
        if (selectedPost) {
          this.query = selectedPost.title; // 将点击的项的标题设置为输入框的内容
          this.$emit('search-id', postId); // 发射选中的 postId
          this.hideDropdown(); // 隐藏下拉列表
          this.$router.push(selectedPost.path);//路由跳转
        }
      },
      showDropdown() {
        this.showDropdownList = true;
      },
      hideDropdown() {
        this.showDropdownList = false;
      },
      hideDropdownWithDelay() {
        // 清除之前的定时器
        if (this.hideDropdownTimeout) {
          clearTimeout(this.hideDropdownTimeout);
        }
        // 延迟隐藏下拉列表以允许点击事件触发
        this.hideDropdownTimeout = setTimeout(() => {
          this.hideDropdown();
        }, 200); // 可以根据需要调整延迟时间
      },
    },
    watch: {
      query(newQuery) {
        // 当 query 变为空时执行刷新
        if (newQuery === '') {
          window.location.reload();
        } else {
          this.onSearch();
        }
      },
    },
  };
  </script>

  <style scoped>
  .search-container {
    position: relative;
    width: 100%;
  }
  input {
    position: absolute;
    left: 20%;
    top: 60px;
    padding: 12px;
    font-size: 16px;
    width: 60%;
    border: 1px solid #636363;
    border-radius: 20px; /* 圆角效果 */
    background: rgba(255, 255, 255, 0.4); /* 半透明效果 */
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3); /* 轻微的阴影效果 */
    transition: background-color 0.3s ease, border-color 0.3s ease; /* 平滑过渡效果 */
    transition: all 0.3s ease;
  }
  input:hover{
    border: 1px solid #ebebeb;
    background: rgba(255, 255, 255, 0.6);
    box-shadow: 0 5px 10px rgba(255, 255, 255, 0.481);
    transition: all 0.3s ease;
  }
  input:focus {
    border: 1px solid #ff6600; /* 聚焦时边框颜色 */
    background: rgba(255, 255, 255, 0.9); /* 聚焦时背景色变为不透明 */
    box-shadow: 0 5px 10px rgba(238, 195, 155, 0.53);
    outline: none; /* 移除默认的蓝色轮廓线 */
    transition: all 0.3s ease;
  }
  .dropdown {
    position: absolute;
    top: 104px;
    left: 22%;
    width: 56%;
    border-radius: 0 0 20px 20px; /* 下拉区域圆角 */
    max-height: 300px; /* 限制下拉区域的最大高度 */
    overflow-y: auto; /* 超出部分滚动 */
    z-index: 1000; /* 确保下拉区域在其他内容之上 */
    backdrop-filter: blur(10px);
    box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  }
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  li {
    padding: 10px;
    cursor: pointer;
    color: rgb(242, 151, 81);
    font-weight: bold;
    transition: all 0.3s ease;
  }
  li:hover {
    background-color: #eef1f18d; /* 悬停时背景颜色 */
    text-decoration: underline;
    text-decoration: none; /* 确保悬停时没有下划线 */
    transition: all 0.3s ease;
  }
  p {
    position: absolute;
    top: 95px;
    left: 25%;
    width: 50%;
    color: #d0d3d1;
    margin-top: 10px;
    font-size: 12px;
  }
  @media (max-width: 620px) {
    input{
      font-size: 8px;
    }
  }
  </style>
  