<template>
 <div class="background color-change">
  <div class="blog-page color-change">
    <h1>王梓瑞的博客</h1>
    <div 
      v-for="(blog, index) in blogs" 
      :key="index" 
      class="blog-post" 
      v-intersect="onIntersect">
      <h2>{{ blog.title }}</h2>
      <p v-html="blog.content"></p>
    </div>
  </div>
 </div>
</template>

<script>
export default {
  data() {
    return {
      blogs: [
        { title: '关于我的博客', content: '网站标签栏图标来自游戏半条命，“空行555”是我的网名，logo圆圈内的“空”来自毛主席的字体。' },
        { title: '网站作为自己的一个前端项目', content: '主要功能用来展示文艺收藏，附加博客、留言、音乐播放器、登录等功能。' },
        { title: 'HTML速写之Emmet语法规则', content: '<a href="https://blog.csdn.net/qq_33744228/article/details/80910377?spm=1001.2014.3001.5506">跳转</a>' },
        { title: '三分钟教你如何用Github找开源项目--值得一看！', content: '<a href="https://blog.csdn.net/as480133937/article/details/105611577?spm=1001.2014.3001.5506">跳转</a>' },
        { title: '知识点整理', content: '1.vue-clivue指令<br>2.vue组件系统<br>3.vue-router<br>4.vuex<br>5.vue插件<br>6.axios<br>7.webpack<br>8.vue前端框架（muse-ui, element-ui）<br>9.sass，less, stylus' },
        { title: 'vue的其他特性', content: '1.异步批量的dom更新：避免一个数据产生多余的dom操作。<br>2.动画系统：使定义animation，transition变得更加单的同时还可以使用钩子函数对动画进行控制。<br>3.可扩展性：自定义指令，过滤器，和组件，还有mixins的可以多个组件中复用共同的特性。' },
        { title: '善用h（createVNode）和render 函数', content: '要实现一个弹窗组件,通常的思路是写一个组件在项目中引用进来，通过v-model来控制他的显示隐藏，但是这样有个问题，我们复用的时候的成本需要复制粘贴。我们没有办法来提高效率，比如封装成npm，通过调用js来使用。然而，有了 createVNode 和render 之后所有问题就迎刃而解了。' },
        { title: 'Vue项目组成逻辑', content: '<a href="https://www.bilibili.com/video/BV1dt4y1K7BF/?spm_id_from=333.999.0.0&vd_source=89fc115a5f0591549338d53178361cb5">跳转</a>' },
        { title: '计算属性和侦听器', content: '1、计算属性<br>模板内的表达式非常便利，但是设计它们的初衷是用于简单运算的。在模板中放入太多的逻辑会让模板过重且难以维护。所以，对于任何复杂逻辑，都应当使用计算属性。<br>1.1 例子：先声明了一个计算属性xx。然后在Vue实例定义中（computed:下面）提供的函数将用作属性xx的 getter 函数，从而实现一些复杂的逻辑，如某些依赖关系。当然，我们可以像绑定普通属性一样在模板中绑定计算属性。<br>1.2 计算属性缓存 vs 方法：我们可以通过定义Vue实例中方法（methods:下面）来实现上述例子的方法。然而，不同的是计算属性是基于它们的响应式依赖进行缓存的。只在相关响应式依赖发生改变时它们才会重新求值。相比之下，每当触发重新渲染时，调用方法将总会再次执行函数。<br>1.3 计算属性 vs 侦听属性：Vue 提供了一种更通用的方式来观察和响应 Vue 实例上的数据变动：侦听属性。当有一些数据需要随着其它数据变动而变动时，很容易滥用watch——特别是如果之前使用过 AngularJS。然而，通常更好的做法是使用计算属性而不是命令式的watch回调。<br>1.4 计算属性的setter：计算属性默认只有 getter ，不过在需要时也可以提供一个 setter。 <br>2、侦听器<br>虽然计算属性在大多数情况下更合适，但有时也需要一个自定义的侦听器。这就是为什么 Vue 通过watch 选项提供了一个更通用的方法，来响应数据的变化。当需要在数据变化时执行异步或开销较大的操作时，这个方式是最有用的。除了watch选项之外，还可以使用命令式的 vm.$watch API。' },
        { title: '条件渲染', content: '1、v-if<br>v-if指令用于条件性地渲染一块内容。这块内容只会在指令的表达式返回 truthy 值的时候被渲染，还可以配合v-else使用。<br>1.1 在 template 元素上使用v-if条件渲染分组：因为v-if是一个指令，所以必须将它添加到一个元素上。但是如果想切换多个元素呢？此时可以把一个 template 元素当做不可见的包裹元素，并在上面使用v-if。最终的渲染结果将不包含 template 元素。<br>1.2 v-else：可以使用v-else 指令来表示v-if 的“else 块”。注意，v-else元素必须紧跟在带v-if或者v-else-if 的元素的后面，否则它将不会被识别。<br>1.3 v-else-if：顾名思义，充当v-if的“else-if 块”，可以连续使用。类似于v-else，v-else-if也必须紧跟在带v-if或者v-else-if 的元素的后面。<br>1.4 用key管理可复用的元素：Vue 会尽可能高效地渲染元素，通常会复用已有元素而不是从头开始渲染。这么做除了使 Vue 变得非常快之外，还有其它一些好处。Vue 提供了一种方式来表达“这两个元素是完全独立的，不要复用它们”。只需添加一个具有唯一值的key属性即可。<br>2、v-show<br>另一个用于根据条件展示元素的选项是v-show指令。不同的是带有v-show 的元素始终会被渲染并保留在 DOM 中。v-show 只是简单地切换元素的 CSS 属性display。注意，v-show 不支持 template 元素，也不支持v-else。<br>3、v-if vs v-show<br>v-if是“真正”的条件渲染，因为它会确保在切换过程中条件块内的事件监听器和子组件适当地被销毁和重建。v-if也是惰性的：如果在初始渲染时条件为假，则什么也不做——直到条件第一次变为真时，才会开始渲染条件块。相比之下，v-show就简单得多——不管初始条件是什么，元素总是会被渲染，并且只是简单地基于 CSS 进行切换。一般来说，v-if 有更高的切换开销，而v-show有更高的初始渲染开销。因此，如果需要非常频繁地切换，则使用v-show较好；如果在运行时条件很少改变，则使用v-if较好。<br>4、v-if 与v-for一起使用<br>官网不推荐同时使用v-if 和v-for。当v-if 与v-for 一起使用时，v-for具有比v-if更高的优先级。这意味着 v-if 将分别重复运行于每个 v-for 循环中。当你只想为部分项渲染节点时，这种优先级的机制会十分有用。' },
        { title: '全局API', content: 'Vue.extend<br>Vue.nextTick<br>Vue.set<br>Vue.delete<br>Vue.directive<br>Vue.mixin<br>Vue.compile<br>Vue.filter<br>Vue.component<br>Vue.use<br>Vue.version' },
        { title: 'VUE实用组件', content: '<a href="https://segmentfault.com/a/1190000010598338">跳转</a>' },
        { title: 'vue插件总结', content: '<a href="https://segmentfault.com/a/1190000039027263">跳转</a>' },
      ],
    };
  },
  methods: {
    onIntersect(entries) {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('bubble-up');
        }
      });
    }
  },
  directives: {
    intersect: {
      mounted(el, binding) {
        const options = {
          threshold: 0.1
        };
        const observer = new IntersectionObserver(binding.value, options);
        observer.observe(el);
      }
    }
  }
};
</script>

<style scoped>
@import '@/assets/css/dark.css';
.background{
  width: 100%;
  margin: 0;
  padding: 50px;
  background-color: #0a6e62;
  position: relative;
}
.blog-page {
  width: 70%;
  margin: 0 auto;
  background-color: #0a6e62;
}

h1 {
  color: wheat;
  text-align: center;
  margin-bottom: 30px;
}

.blog-post {
  color: wheat;
  margin: 50px 0;
  padding: 20px;
  border-radius: 10px;
  opacity: 0;
  transform: translateY(50px);
  backdrop-filter: blur(10px);
  box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  transition: transform 0.6s ease, opacity 0.6s ease;
}

.blog-post.bubble-up {
  opacity: 1;
  transform: translateY(0);
}
</style>
