<template>
    <div class="music-player">
      <div class="track-info">
        <p style="font-size: 10px;">{{ currentTrack.title }}</p>
        <p style="font-size: 10px;">{{ currentTrack.artist }}</p>
      </div>
      <div class="controls">
        <button @click="previousTrack" class="control-button">
          <img :src="require('@/assets/img/Previous.png')" alt="Previous Track" />
        </button>
        <button @click="togglePlayPause" class="control-button">
          <img :src="isPlaying ? require('@/assets/img/Play.png') : require('@/assets/img/Pause.png')" alt="Play/Pause" />
        </button>
        <button @click="nextTrack" class="control-button">
          <img :src="require('@/assets/img/Next.png')" alt="Next Track" />
        </button>
        <button @click="toggleTrackList" class="control-button">
          <img src="/List.png" alt="Show Track List" />
        </button>
      </div>
      <div v-if="showTrackList" class="track-list">
        <ul>
          <li
            v-for="(track, index) in tracks"
            :key="track.id"
            @click="selectTrack(index)"
            :class="{ 'is-current': index === currentTrackIndex }"
          >
            {{ track.title }} - {{ track.artist }}
          </li>
        </ul>
      </div>
      <audio ref="audioElement" @ended="nextTrack"></audio>
    </div>
    <div v-if="showTrackList" class="overlay" @click="toggleTrackList"></div>
  </template>
  
  <script>
  import { ref, computed, onMounted } from 'vue';
  
  export default {
    name: 'MusicPlayer',
    setup() {
      const tracks = ref([
        { id: 1, title: 'Let it Be', artist: 'The Beatles', src: require('@/assets/music/Let it Be.mp3') },
        { id: 2, title: 'In My Life', artist: 'The Beatles', src: require('@/assets/music/In My Life.mp3') },
        { id: 3, title: 'California Dreamin', artist: 'The Mamas & The Papas', src: require('@/assets/music/California Dreamin.mp3') },
        { id: 4, title: 'Heroes', artist: '大卫·鲍伊', src: require('@/assets/music/Heroes.mp3') },
        { id: 5, title: '圣诞快乐，劳伦斯', artist: '坂本龙一', src: require('@/assets/music/Merry Christmas Mr. Lawrence.mp3') },
        { id: 6, title: '德彪西《月光》', artist: '里赫特', src: require('@/assets/music/德彪西《月光》.mp3') },
        { id: 7, title: 'Bloody Mary Girl', artist: 'She Her Her Hers', src: require('@/assets/music/Bloody Mary Girl.mp3') },
        { id: 8, title: '新长征路上的摇滚', artist: '崔健', src: require('@/assets/music/新长征路上的摇滚.mp3') },
        { id: 9, title: 'Episode 33', artist: 'She Her Her Hers', src: require('@/assets/music/Episode 33.mp3') },
        { id: 10, title: 'Floating city', artist: 'She Her Her Hers', src: require('@/assets/music/Floating city.mp3') },
        { id: 11, title: 'Dead in the Water', artist: 'Noel Gallagher', src: require('@/assets/music/Dead in the Water.mp3') },
        { id: 12, title: 'Hey Jude', artist: 'The Beatles', src: require('@/assets/music/Hey Jude.mp3') },
        { id: 13, title: '电动少女', artist: 'Chinese Football', src: require('@/assets/music/电动少女.mp3') },
        { id: 14, title: '花房姑娘', artist: '崔健', src: require('@/assets/music/花房姑娘.mp3') },
        { id: 15, title: 'San Francisco', artist: 'Scott McKenzie', src: require('@/assets/music/San Francisco.mp3') },
        { id: 16, title: 'Streets Of Love', artist: '滚石乐队', src: require('@/assets/music/Streets Of Love.mp3') },
        { id: 17, title: 'Stand by Me', artist: '约翰列侬', src: require('@/assets/music/Stand by Me.mp3') },
        { id: 18, title: 'Summersong', artist: 'The Decemberists', src: require('@/assets/music/Summersong.mp3') },
        { id: 19, title: 'Take Me Home Country Roads', artist: '约翰丹佛', src: require('@/assets/music/Take Me Home Country Roads.mp3') },
        { id: 20, title: 'Yellow', artist: 'Coldplay', src: require('@/assets/music/Yellow.mp3') },
        { id: 21, title: 'Wake Me up When September Ends', artist: '绿日乐队', src: require('@/assets/music/Wake Me up When September Ends.mp3') },
        { id: 22, title: 'Boulevard Of Broken Dreams', artist: '绿日乐队', src: require('@/assets/music/Boulevard Of Broken Dreams.mp3') },
        { id: 23, title: 'Don\'t Look Back In Anger', artist: 'oasis', src: require('@/assets/music/Don\'t Look Back In Anger.mp3') },
        { id: 24, title: 'I\'ll Be Your Mirror', artist: '地下丝绒', src: require('@/assets/music/I\'ll Be Your Mirror.mp3') },
        { id: 25, title: 'Live Forever', artist: 'oasis', src: require('@/assets/music/Live Forever.mp3') },
        { id: 26, title: '弯弯的月亮', artist: '刘欢', src: require('@/assets/music/弯弯的月亮.mp3') },
      ]);
      const currentTrackIndex = ref(0);
      const isPlaying = ref(false);
      const showTrackList = ref(false);
      const audioElement = ref(null);
  
      const currentTrack = computed(() => tracks.value[currentTrackIndex.value]);
  
      function togglePlayPause() {
        if (isPlaying.value) {
          audioElement.value.pause();
        } else {
          audioElement.value.play();
        }
        isPlaying.value = !isPlaying.value;
      }
  
      function nextTrack() {
        if (currentTrackIndex.value < tracks.value.length - 1) {
          currentTrackIndex.value++;
        } else {
          currentTrackIndex.value = 0;
        }
        loadTrack();
        audioElement.value.play();
        isPlaying.value = true;
      }
  
      function previousTrack() {
        if (currentTrackIndex.value > 0) {
          currentTrackIndex.value--;
        } else {
          currentTrackIndex.value = tracks.value.length - 1;
        }
        loadTrack();
        audioElement.value.play();
        isPlaying.value = true;
      }
  
      function toggleTrackList() {
        showTrackList.value = !showTrackList.value;
      }
  
      function selectTrack(index) {
        currentTrackIndex.value = index;
        loadTrack();
        audioElement.value.play();
        isPlaying.value = true;
      }
  
      function loadTrack() {
        audioElement.value.src = currentTrack.value.src;
      }
  
      onMounted(() => {
        loadTrack();
      });
  
      return {
        tracks,
        currentTrack,
        currentTrackIndex,
        isPlaying,
        showTrackList,
        togglePlayPause,
        nextTrack,
        previousTrack,
        toggleTrackList,
        selectTrack,
        audioElement,
      };
    },
  };
  </script>
  
  <style scoped>
  html, body {
    height: 100%; /* 确保高度占满整个视窗 */
    margin: 0;
    padding: 0;
    overflow-x: hidden; /* 禁止水平滚动 */
    overflow-y: auto; /* 允许垂直滚动 */
    position: relative; /* 添加定位属性 */
  }
  .music-player {
    display: flex;
    position: absolute;
    top: 1.5%;
    left: 70%;
    z-index: 1000;
    align-items: center;
    border-radius: 20px;
    width: 18%;
    backdrop-filter: blur(10px);
    box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
    padding: 5px;
    margin: 0 8px;
  }
  
  .track-info {
    flex: 2;
