    flex: 1;
  }
  
  .control-button {
    background: rgba(255, 255, 255, 0.2);
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin: 0 5px;
    transition: background 0.3s, transform 0.2s;
    position: relative;
    flex-shrink: 0;
  }
  
  .control-button:hover {
    background: rgba(255, 255, 255, 0.5);
    transform: scale(1.1);
  }
  
  .control-button img {
    width: 100%;
    height: auto;
    object-fit: contain;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .track-list {
    position: absolute;
    width: 90%;
    top: 2.5em;
    left: 5%;
    border-radius: 10px;
    padding: 10px;
    max-height: 200px;
    overflow-y: auto;
    z-index: 1000;
    backdrop-filter: blur(10px);
    box-shadow: inset 1px 1px 6px rgba(255, 255, 255, 0.3),2px 2px 15px rgba(0, 0, 0, 0.5);
  }
  
  .track-list li {
    padding: 5px;
    cursor: pointer;
    transition: background 0.3s;
    border-radius: 8px;
  }
  
  .track-list li:hover {
    background: rgba(213, 231, 231, 0.5);
  }
  
  .is-current {
    font-weight: bold;
    color: #d5eae5;
  }
  
  @media (max-width: 1315px) {
    .track-list {
    top: 2.5em;
    }
  }
  @media (max-width: 1303px) {
    .track-list {
    top: 3em;
    }
  }
  @media (max-width: 1215.5px) {
    .track-list {
    top: 3.7em;
    }
  }
  @media (max-width: 1160px) {
    .music-player {
    top: 55%;
    left: 79%;
    width: 20%;
    flex-wrap: wrap;
    }
    .track-list {
    top: 3em;
    }
  }
  @media (max-width: 1093px) {
    .track-list {
    top: 3.7em;
    }
  }
  @media (max-width: 1085px) {
    .track-list {
    top: 3.9em;
    }
  }
  @media (max-width: 1068px) {
    .track-list {
